.hero_details_wrap {
  display: grid;
  grid-template-columns: 1fr;
  background: #2b2c32;
  
  color: #989899;
}
  .hero_details_wrap .nav_panel{
    display: grid;
    grid-template-columns: 1fr 3fr;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .hero_details_wrap .nav_panel a {
      text-decoration: none;
      font-size: 2em;
      color: #989899;
      font-weight: 500;
      padding-left: 20px;
    }
  .hero_details_wrap .nav_panel a:hover {
        color: #d2d2d2;
      }
  .hero_details_wrap h1 {
    text-align: center;
  }
  .hero_details_wrap ul {
    -webkit-padding-start: 0;
            padding-inline-start: 0;
  }
  .hero_details_wrap li {
    
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(76vh, auto);
  }
  .hero_details_wrap li div {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
              justify-content: center;

      /* align-items: center; */
    }
  .hero_details_wrap li img {
      width: 35vw;
      -webkit-align-self: center;
              align-self: center;
      margin: 0 auto;

    }

.str, .agi, .int {
    position: relative;
    padding-left: 26px;
}

.str::before {
    content: "";
    position: absolute;
    left: 5px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: rgb(244, 67, 54);
  }

.agi::before {
    content: "";
    position: absolute;
    left: 5px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: rgb(57, 212, 2);
  }

.int::before {
    content: "";
    position: absolute;
    left: 5px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: rgb(1, 169, 244);
  }

@media (max-width: 768px) {
      
      .hero_details_wrap ul li {
        grid-template-columns:  1fr;
    grid-auto-rows: minmax(30vh, auto);
      }

        .hero_details_wrap ul li img {
          width: 80%;
        }
        .hero_details_wrap ul li div {
            text-align: center;
        }
}
