.search {
  grid-row: 2;
  margin-left: 7px;
  display: grid;  
  margin: 0 auto;
}
  .search input {
    width: 99%;
    outline:none;
    border-radius: 6px;
    padding: 10px;
    border: none;
    margin: 0 auto;
    -webkit-align-self: center;
            align-self: center;
    margin-top: 20px;
  }
  .search ul {
      display: grid;
      grid-template-columns: repeat(8, 1fr);
      list-style-type: none;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-padding-start: 0;
              padding-inline-start: 0;
  }
  .search ul a {
        color: #989899;
        text-decoration: none;
      }
  .search ul li {
        margin: 0 auto;
        width: 100%;
        padding-top: 15px;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-box-align: center;
        -webkit-align-items: center;
                align-items: center;
      }
  .search ul li:hover {
          background: #222328;
        }
  .search ul li img {
          max-width: 11vw;
          width: 100%;
        }
  .search ul li div {
          text-align: center;
        }

.highlight {
  background: #98989957;
}
